export const formatTime = (num) => {
  let mm = parseInt(num / 60)
  let ss = parseInt(num - mm * 60)
  ss = ss < 10 ? `0${ss}` : ss
  // let ms = (num - mm*60 -s ).toFixed(2)
  return `${mm}:${ss}`
}

export const formatTimeStr = (num) => {
  let mm = parseInt(num / 60)
  let ss = parseInt(num - mm * 60)
  ss = ss < 10 ? `0${ss}` : ss
  let ms = (num - mm * 60 - ss).toFixed(3).substr(1, 4)
  return `${mm}:${ss}${ms}`
}

/**
 * Parse lrc, suppose multiple time tag
 *
 * @param {String} lrc_s - Format:
 * [mm:ss.xx]lyric
 * [mm:ss.xxx]lyric
 * [mm:ss.xx][mm:ss.xx][mm:ss.xx]lyric
 *
 * @return {String} [[time, text], [time, text], [time, text], ...]
 */
export const parseLrc = (lrcs) => {
  const lyric = lrcs.split('\n')
  let lrc = []
  const lyricLen = lyric.length
  for (let i = 0; i < lyricLen; i++) {
    // match lrc time
    const lrcTimes = lyric[i].match(/\[(\d{2}):(\d{2})\.(\d{2,3})]/g)
    // match lrc text
    const lrcText = lyric[i].replace(/\[(\d{2}):(\d{2})\.(\d{2,3})]/g, '').replace(/^\s+|\s+$/g, '')

    if (lrcTimes != null) {
      // handle multiple time tag
      const timeLen = lrcTimes.length
      for (let j = 0; j < timeLen; j++) {
        const oneTime = /\[(\d{2}):(\d{2})\.(\d{2,3})]/.exec(lrcTimes[j])
        const lrcTime = (oneTime[1]) * 60 + parseInt(oneTime[2]) + parseInt(oneTime[3]) / ((oneTime[3] + '').length === 2 ? 100 : 1000)
        lrc.push([lrcTime, lrcText])
      }
    }
  }
  // sort by time
  lrc.sort((a, b) => a[0] - b[0])
  return lrc
}
/* 代替eval */
export const evil = (fn) => {
  var Fn = Function // 一个变量指向Function，防止有些前端编译工具报错

  return new Fn('return ' + fn)()
}



